.search {
    text-align: center;
    padding: 1rem;
    background: transparent;
}

.search input {
    padding: .7rem 1.5rem;
    font-size: 1.2rem;
    border-radius: 25px;
    border: 1px solid rgba(255, 255, 255, 0.8);
    background: rgba(255, 255, 255, 0.1);
    color: #f8f8f8;
}

::placeholder {
    color: #f8f8f8;
}